.mapArea {
  margin-top: 20px;

  .map {
    background-color: #ccebf8;
    height: 740px;
    padding-top: 30px;
    position: relative;

    .weather {
      width: 50%;
      height: 626px;
      border-right: 1px solid #ffffff;
      position: relative;
      background: url(../../asset/images/map.png) no-repeat 0 /cover;
    }

    .dust {
      width: 50%;
      height: 626px;
      position: absolute;
      top: 30px;
      right: 0;
    }

    .maptab {
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 999;

      li {
        a {
          border: 1px solid #8c9091;
          color: #3e3a39;
          font-size: 16px;
          width: 90px;
          height: 36px;
          background-color: #fff;
          line-height: 36px;
          text-align: center;
          font-weight: 600;

          &.active {
            background-color: #0047ba;
            color: #fff;
            border: 1px #0047ba solid;
          }
        }
      }
    }
  }
}



:is(.mapArea .map) .weather,
.dust {
  .maptitle {
    position: absolute;
    top: 0px;
    left: 25px;
    width: 200px;
    height: 54px;

    .title {
      line-height: 22px;
      font-weight: 600;
      font-size: 22px;
      z-index: 998;
    }

    .titleDate {
      .date {
        line-height: 30px;
        font-weight: 600;
        font-size: 20px;
      }

      .time {
